<template>
    <div>
        <Menu theme="dark" class="aside" active-name="goods-list" @on-select="changeMenuItem" width="200px" accordion>
            <MenuItem name='person' class="person">
                <div class="account">
                    <Avatar :src="this.$store.state.currUser.avatar" icon="person" size="large" />
                    <p>{{ this.$store.state.currUser.name }}</p>
                </div>
                <div v-if="currUserFlag" class="change-account">
                    <Button @click.native="logoutAccount" type="primary"></Button>
                    <Button @click.native="changeAccount" type="primary"></Button>
                </div>
                <div v-else class="change-account">
                    <Button @click.native="changeAccount" type="primary"></Button>
                </div>
            </MenuItem>
            <MenuItem name="goods-list">
                <Icon type="android-list"></Icon> 
            </MenuItem>
            <Submenu name="2">
                <template slot="title">
                    <Icon type="settings"></Icon>
                    
                </template>
                <MenuItem name="goods-storage"></MenuItem>
                <MenuItem name="goods-detail"></MenuItem>
            </Submenu>
            <MenuItem name="cash-register">
                <Icon type="calculator"></Icon> 
            </MenuItem>
            <MenuItem name="message-list">
                <Icon type="android-mail"></Icon>  
            </MenuItem>
            <MenuItem name="data-statistics">
                <Icon type="podium"></Icon> 
            </MenuItem>
            <MenuItem name="person-list">
                <Icon type="ios-people"></Icon>  
            </MenuItem>
            <MenuItem name="todo-list">
                <Icon type="ios-alarm"></Icon> 
            </MenuItem>
        </Menu>
        <!--  -->
        <Modal title="" class-name="login-in" v-model="loginFlag" ok-text="" width="300">
            <Form ref="login" :model="user" :rules="checkUser">
                <FormItem prop="coding">
                    <Input type="text" v-model="user.coding" placeholder="">
                        <Icon type="person" slot="prepend"></Icon>
                    </Input>
                </FormItem>
                <FormItem prop="password">
                    <Input type="password" v-model="user.password" placeholder="">
                        <Icon type="locked" slot="prepend"></Icon>
                    </Input>
                </FormItem>
            </Form>
            <p style="text-align: center;">000admin</p>
            <div slot="footer">
                <Button @click.native="cancelLogin" type="text"></Button>
                <Button @click.native="loginSubmit" type="primary"></Button>
            </div>
        </Modal>
    </div>
</template>

<script>

import IndexedDB from '../indexedDB/IndexedDB'

export default {
    data () {
        // 
        const checkCoding = (rule, value, callback) => {
            const reg = /^[0-9]\d*$/g;
            if (value === '') {
                callback(new Error(''));
            } else {
                if (reg.test(value)) {
                    callback();
                } else {
                    callback(new Error(''));
                }
            }
        }
        return {
            loginFlag: true,
            user: {
                coding: '',
                password: ''
            },
            checkUser: {
                coding: { required: true, validator: checkCoding, trigger: 'blur' },
                password: { required: true, message: '', trigger: 'blur' },
            }
        }
    },
    computed: {
        currUserFlag: function () {
            if (this.$store.state.currUser.coding) {
                return true;
            } else {
                return false;
            }
        }
    },
    methods: {
        // 
        changeMenuItem (name) {
            switch(name) {
                case 'person':
                    // this.loginFlag = true;
                    break;
                case 'goods-list':
                    this.$router.push({
                        path: '/'
                    });
                    break;
                case 'goods-storage':
                    this.$router.push({
                        path: '/goodsStorage'
                    });
                    break;
                case 'goods-detail':
                    this.$router.push({
                        path: '/goodsDetailList'
                    });
                    break;
                case 'cash-register':
                    this.$router.push({
                        path: '/cashRegister'
                    });
                    break;
                case 'message-list':
                    this.$store.commit('resetMessageNumber');
                    let _this = this;
                    let vshopDB = null;
                    IndexedDB.openDB('vshopDB', 1, vshopDB, {
                        name: 'vshop',
                        key: 'name'
                    }, function (db) {
                        let vshopDB = db;
                        IndexedDB.putData(vshopDB, 'vshop', [_this.$store.state.messages]);
                    });
                    this.$router.push({
                        path: '/messageList'
                    });
                    break;
                case 'data-statistics':
                    this.$router.push({
                        path: '/dataStatistics'
                    });
                    break;
                case 'person-list':
                    this.$router.push({
                        path: '/personList'
                    });
                    break;
                case 'todo-list':
                    this.$router.push({
                        path: '/todoList'
                    });
                    break;
                default:
                    break;
            }
        },
        // 
        loginSubmit () {
            const users = this.$store.state.users.userList;
            let loginFlag = true;
            this.$refs.login.validate((valid) => { 
                if (valid) { 
                    for (let i = 0, len = users.length; i < len; i++) {
                        if (users[i].coding === this.user.coding) {
                            if (users[i].password === this.user.password) {
                                // users[i]
                                this.$store.commit('setCurrUser', users[i]);
                                this.$Message.success('');
                                this.loginFlag = false;
                                loginFlag = false;
                                this.$refs['login'].resetFields();
                            } else {
                                this.$Message.error(''); 
                                this.user.password = '';
                                loginFlag = false;
                            }
                        }
                    }
                    if (loginFlag) {
                        this.$Message.error(''); 
                        this.$refs['login'].resetFields();
                    }
                } else { 
                    this.$Message.error(''); 
                } 
            });
        },
        // 
        cancelLogin () {
            this.loginFlag = false;
        },
        // 
        logoutAccount () {
            this.$Modal.confirm({
                title: '',
                content: '<h3></h3><p><p>',
                onOk: () => {
                    this.$store.commit('logoutAccount');
                    this.$Message.error(''); 
                }
            });
        },
        // 
        changeAccount () {
            this.loginFlag = true;
        }
    }
}

</script>

<style>
    .aside {
        height: 100%;
        overflow-y: scroll;
    }
    .person:hover .change-account {
        display: block;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .person .change-account {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
        background-color: rgba(73, 80, 96, 0.8);
        display: none;
    }
    .change-account button {
        margin: 5px;
    }
    .account {
        display: flex;
        align-items: center;
    }
    .account p {
        margin-left: 2em;
    }
    .login-in {
        background: url('../assets/login-bg.jpg') no-repeat center fixed;
    }
</style>



