<template>
    <div class="person-list">
        <div class="add-person">
            <Button @click.native="changeUserFlag" type="primary" icon="person-add"></Button>
        </div>
        <div class="person-message">
            <Table :columns="userTable" :data="userList"></Table>
        </div>
        <!--  -->
        <Modal title="" v-model="addUserFlag" :styles="{top: '20px'}" width="400"> 
            <Form ref="newUser" :model="newUser" :rules="checkNewUser" :label-width="100"> 
                <FormItem label="" prop="name"> 
                    <Input v-model="newUser.name" placeholder=""></Input> 
                </FormItem> 
                <FormItem label="" prop="coding"> 
                    <Input v-model="newUser.coding" placeholder=""></Input> 
                </FormItem> 
                <FormItem label="" prop="position"> 
                    <Input v-model="newUser.position" placeholder=""></Input> 
                </FormItem> 
                <FormItem label="" prop="password"> 
                    <Input type="password" v-model="newUser.password" placeholder=""></Input> 
                </FormItem> 
                <FormItem label="" prop="password2"> 
                    <Input type="password" v-model="newUser.password2" placeholder=""></Input> 
                </FormItem> 
                <FormItem label="" prop="jurisdiction"> 
                    <Select v-model="newUser.jurisdiction" placeholder=""> 
                        <Option value=""></Option> 
                        <Option value=""></Option> 
                        <Option value=""></Option>
                    </Select> 
                </FormItem>
                <FormItem label="" prop="telephone"> 
                    <Input v-model="newUser.telephone" placeholder=""></Input> 
                </FormItem> 
                <FormItem label=""> 
                    <Input v-model="newUser.address" placeholder=""></Input> 
                </FormItem>
                <FormItem label=""> 
                    <Button @click="clickSelectImg" type="ghost" icon="ios-cloud-upload-outline"></Button>
                    <input @change="selectImg($event)" class="file-btn" type="file" style="visibility: hidden;">
                    <div class="img-place">
                        <!--  -->
                    </div>
                </FormItem> 
            </Form>
            <div slot="footer">
                <Button @click.native="submitAddUser" type="primary"></Button>
                <Button @click.native="resetAddUser" type="text"></Button>
            </div>
        </Modal>
    </div>
</template>

<script>

import IndexedDB from '../indexedDB/IndexedDB'

export default {
    data () {
        // 
        const checkUserCoding = (rule, value, callback) => {
            const reg = /^[0-9]\d*$/g;
            if (value === '') {
                callback(new Error(''));
            } else {
                if (reg.test(value)) {
                    const users = this.$store.state.users.userList;
                    for (let i = 0, len = users.length; i < len; i++) {
                        if (users[i].coding === value) {
                            callback(new Error(''));
                        }
                    }
                    callback();
                } else {
                    callback(new Error(''));
                }
            }
        }
        // 
        const checkPassword2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                if (value === this.newUser.password) {
                    callback();
                } else {
                    callback(new Error(''));
                }
            }
        }
        // 
        const checkTelephone = (rule, value, callback) => {
            const reg = /^1[34578]\d{9}$/;
            if (value === '') {
                callback(new Error(''));
            } else {
                if (reg.test(value)) {
                    callback();
                } else {
                    callback(new Error(''));
                }
            }
        }
        return {
            addUserFlag: false,
            newUser: {
                name: '',
                coding: '',
                position: '',
                password: '',
                password2: '',
                jurisdiction: '',
                telephone: '',
                address: '',
                avatar: ''
            },
            checkNewUser: {
                name: { required: true, message: '', trigger: 'blur' },
                coding: { required: true, validator: checkUserCoding, trigger: 'blur' },
                position: { required: true, message: '', trigger: 'blur' },
                password: [
                    { required: true, message: '', min: 6, trigger: 'blur' },
                    { min: 6, message: '',  trigger: 'blur' },
                ],
                password2: { required: true, validator: checkPassword2, trigger: 'blur' },
                jurisdiction: { required: true, message: '', trigger: 'change' },
                telephone: { required: true, validator: checkTelephone, trigger: 'blur' },
            },
            // 
            userTable: [
                {
                    title: '',
                    width: 100,
                    align: 'center',
                    key: 'name'
                },
                {
                    title: '',
                    width: 100,
                    align: 'center',
                    key: 'position'
                },
                {
                    title: '',
                    width: 100,
                    align: 'center',
                    key: 'coding'
                },
                {
                    title: '',
                    width: 100,
                    align: 'center',
                    key: 'jurisdiction'
                },
                {
                    title: '',
                    width: 200,
                    align: 'center',
                    key: 'telephone'
                },
                {
                    title: '',
                    align: 'center',
                    key: 'address'
                },
                {
                    title: '',
                    align: 'center',
                    key: 'action',
                    width: 100,
                    render: (h, params) => {
                        return h('Button', {
                            props: {
                                type: 'warning',
                                size: 'small',
                                icon: 'trash-b'
                            },
                            on: {
                                click: () => {
                                    this.$store.commit('deleteUser', params.index);
                                    let _this = this;
                                    let vshopDB = null;
                                    IndexedDB.openDB('vshopDB', 1, vshopDB, {
                                        name: 'vshop',
                                        key: 'name'
                                    }, function (db) {
                                        let vshopDB = db;
                                        IndexedDB.putData(vshopDB, 'vshop', [_this.$store.state.users]);
                                    });
                                }
                            }
                        }, '');
                    }
                }
            ],
            userList: this.$store.state.users.userList
        }
    },
    methods: {
        changeUserFlag () {
            this.addUserFlag = true;
        },
        // 
        clickSelectImg () {
            document.querySelector('.file-btn').click();
        },
        selectImg (event) {
            let _this = this,
                info = '',
                imgPlace = document.querySelector('.img-place'),
                files = event.target.files,
                type = 'default',
                reader = new FileReader();
            if (/image/.test(files[0].type)){
                reader.readAsDataURL(files[0]);
                type = "image";
            } else {
                reader.readAsText(files[0]);
                type = "text";
            }
            reader.onerror = function(){
                imgPlace.innerHTML = " " + reader.error.code;
            };
            reader.onload = function(){
                let html = "";
                switch(type) {
                    case "image":
                        html = "<img style='width: 100%; height: 100%;' src=\"" + reader.result + "\">";
                        _this.newUser.avatar = reader.result;
                        break;
                    case "text":
                        html = reader.result;
                        break;
                    default:
                        break;
                }
                imgPlace.innerHTML = html;
            };
        },
        // 
        submitAddUser () {
            let _this = this;
            this.$refs['newUser'].validate((valid) => { 
                if (valid) { 
                    var user = this.newUser;
                    delete user.password2;
                    if (this.$store.state.currUser.coding) {
                        if (this.$store.state.currUser.jurisdiction === '') {
                            this.$store.commit('addNewUser', user);
                            this.$Message.success('');
                            this.newUser = {
                                name: '',
                                coding: '',
                                position: '',
                                password: '',
                                password2: '',
                                jurisdiction: '',
                                telephone: '',
                                address: '',
                                avatar: ''
                            };
                            document.querySelector('.img-place').innerHTML = '';
                            let _this = this;
                            let vshopDB = null;
                            IndexedDB.openDB('vshopDB', 1, vshopDB, {
                                name: 'vshop',
                                key: 'name'
                            }, function (db) {
                                let vshopDB = db;
                                IndexedDB.putData(vshopDB, 'vshop', [_this.$store.state.users]);
                            });
                        } else {
                            this.$Message.error('');
                        }
                    } else {
                        this.$Message.error('');
                    }
                } else { 
                    this.$Message.error('!'); 
                } 
            });
        },
        // 
        resetAddUser () {
            this.$refs['newUser'].resetFields(); 
        }
    }
}
</script>

<style scoped>
    .add-person {
        padding: 10px;
    }
    .img-place {
        width: 200px;
        height: 200px;
        margin-top: 10px;
        border: 1px dashed #dddee1;
    }
    .img-place img {
        width: 100%;
        height: 100%;
    }
</style>



