<template>
    <div>
        <h3>
            
            <Button @click.native="changeSetMessageFlag" type="primary" icon="settings" size="small"></Button>
        </h3>
        <Table :columns="messageTable" :data="messageList"></Table>
        <!--  -->
        <Modal title="" v-model="setMessageFlag" width="320"> 
            <Form ref="newLimit" :model="setMessage" :label-width="100"> 
                <FormItem label="" prop="limitNumber"> 
                    <Input v-model.number="setMessage.limitNumber" placeholder=""></Input> 
                </FormItem> 
                <FormItem label="" prop="limitDate"> 
                    <Input v-model.number="setMessage.limitDate" placeholder=""></Input> 
                </FormItem> 
            </Form>
            <div slot="footer">
                <Button @click.native="resetChangeMessage" type="text"></Button>
                <Button @click.native="submitChangeMessage" type="primary"></Button>
            </div>
        </Modal>
    </div>
</template>

<script>

import IndexedDB from '../indexedDB/IndexedDB'

export default {
    data () {
        return {
            setMessageFlag: false,
            pageNum: 1,
            setMessage: {
                limitNumber: '',
                limitDate: ''
            },
            messageTable: [
                {
                    title: '',
                    key: 'date',
                    width: '200px',
                    align: 'center'
                },
                {
                    title: '',
                    key: 'content',
                    align: 'center'
                },
                {
                    title: '',
                    align: 'center',
                    width: '200px',
                    render: (h, params) => {
                        return h('Button', {
                            props: {
                                type: 'warning',
                                size: 'small',
                                icon: 'trash-b'
                            },
                            on: {
                                click: () => {
                                    if (this.$store.state.currUser.coding) {
                                        this.$store.commit('deleteMessage', params.index);
                                        let _this = this;
                                        let vshopDB = null;
                                        IndexedDB.openDB('vshopDB', 1, vshopDB, {
                                            name: 'vshop',
                                            key: 'name'
                                        }, function (db) {
                                            let vshopDB = db;
                                            IndexedDB.putData(vshopDB, 'vshop', [_this.$store.state.messages]);
                                        });
                                    } else {
                                        this.$Message.error('');
                                    }
                                }
                            }
                        }, '')
                    }
                }
            ],
        }
    },
    computed: {
        messageList: function () {
            return this.$store.state.messages.messageList;
        }
    },
    methods: {
        changeSetMessageFlag () {
            this.setMessageFlag = true;
        },
        // 
        submitChangeMessage () {
            if (this.$store.state.currUser.coding) {
                if (this.$store.state.currUser.jurisdiction === '') {
                    this.$store.commit('changeMessage', this.setMessage);
                    this.$Message.success('');
                    this.setMessageFlag = false;
                    let _this = this;
                    let vshopDB = null;
                    IndexedDB.openDB('vshopDB', 1, vshopDB, {
                        name: 'vshop',
                        key: 'name'
                    }, function (db) {
                        let vshopDB = db;
                        IndexedDB.putData(vshopDB, 'vshop', [_this.$store.state.messages]);
                    });
                } else {
                    this.$Message.error('');
                }
            } else {
                this.$Message.error('');
            }
        },
        // 
        resetChangeMessage () {
            this.$refs['newLimit'].resetFields(); 
        }
    }
}
</script>

<style>
    h3 {
        padding: 10px;
    }
    h3 button {
        margin-left: 20px;
    }
    .warning {
        color: red;
    }
    .new-message span {
        color: red;
    }
</style>



