<template>
    <div class="goods-detail">
        <Table :columns="goodsDetailTable" :data="detailGoods" :disabled-hover="true" :border="true"></Table>
        <div>
            <h3></h3> 
            <div id="sales-chart"></div>
        </div>
        <!-- <div>
            <h3></h3>
        </div> -->
        <Modal title="" v-model="setGoodsFlag" :styles="{top: '20px'}" width="400"> 
            <Form :model="setGoods" :rules="checkSetGoods" :label-width="80"> 
                <FormItem label="" prop="name"> 
                    <Input v-model="setGoods.name"></Input> 
                </FormItem> 
                <FormItem label="" prop="price"> 
                    <Input v-model.number="setGoods.price"></Input> 
                </FormItem> 
                <FormItem label="" prop="number"> 
                    <Input v-model.number="setGoods.number"></Input> 
                </FormItem> 
                <FormItem label="" prop="category"> 
                    <Select v-model="setGoods.category" placeholder=""> 
                        <Option value=""></Option> 
                        <Option value=""></Option> 
                        <Option value=""></Option>
                        <Option value=""></Option>
                        <Option value=""></Option>
                        <Option value=""></Option>
                    </Select> 
                </FormItem> 
                <FormItem label="" prop="date"> 
                    <DatePicker type="date" placeholder="" v-model="setGoods.date"></DatePicker> 
                </FormItem>
            </Form>
            <div class="set-goods-footer" slot="footer">
                <Button @click.native="submitChangeGoods" type="success" long></Button>
                <Button @click.native="deleteGoods" type="error" long  title="" style="margin: 10px 0px 0px 0px"></Button>
            </div>
        </Modal>
    </div>
</template>

<script>

import IndexedDB from '../indexedDB/IndexedDB'

import Highcharts from 'highcharts/highstock'
import HighchartsMore from 'highcharts/highcharts-more'
import HighchartsDrilldown from 'highcharts/modules/drilldown'
import Highcharts3D from 'highcharts/highcharts-3d'
import Highmaps from 'highcharts/modules/map'

HighchartsMore(Highcharts) 
HighchartsDrilldown(Highcharts)
Highcharts3D(Highcharts)
Highmaps(Highcharts)

export default {
    data () {
        // 
        const checkPrice = (rule, value, callback) => {
            const reg = /^(\d+\.\d{1,1}|\d+)$/g;
            if (value === '') {
                callback();
            } else if (reg.test(value)) {
                callback();
            } else {
                callback(new Error(''));
            }
        }
        // 
        const checkNumber = (rule, value, callback) => {
            const reg = /^[1-9]\d*$/g;
            if (value === '') {
                callback();
            } else {
                if (reg.test(value)) {
                    callback();
                } else {
                    callback(new Error(''));
                }
            }
        }
        return {
            setGoodsFlag: false,
            setGoods: {
                name: '', 
                price: '', 
                number: '', 
                category: '', 
                date: '', 
            },
            checkSetGoods: {
                price: { validator: checkPrice, trigger: 'blur' },
                number: { validator: checkNumber, trigger: 'blur' }, 
            },
            goodsDetailTable: [
                {
                    title: '',
                    key: 'img',
                    align: 'center',
                    width: 300,
                    className: 'goods-img',
                    render: (h, params) => {
                        return h('img', {
                            attrs: {
                                src: params.row.image,
                            }
                        });
                    }
                },
                {
                    title: '',
                    key: 'message',
                    align: 'center',
                    render: (h, params) => {
                        return h('Table', {
                            props: {
                                columns: this.$data.goodsTable,
                                data: this.$data.goods,
                                showHeader: false,
                                border: true
                            }
                        });
                    }
                },
                {
                    title: '',
                    key: 'aciton',
                    align: 'center',
                    width: 150,
                    render: (h, params) => {
                        return h('Button', {
                            props: {
                                type: 'warning',
                                icon: 'settings',
                            },
                            on: {
                                click: () => {
                                    this.setGoodsFlag = true;
                                }
                            }
                        }, ''); 
                    }
                }
            ],
            detailGoods: [
                {
                    image: this.$store.state.goods.detailGoods.image,
                }
            ],
            goodsTable: [
                {
                    title: '',
                    width: 150,
                    key: 'name',
                    render: (h, params) => {
                        return h('strong', params.row.name);
                    }
                },
                {
                    title: '',
                    key: 'value'
                }
            ],
            goods: [
                {
                    name: '',
                    value: this.$store.state.goods.detailGoods.name
                },
                {
                    name: '',
                    value: this.$store.state.goods.detailGoods.coding
                },
                {
                    name: '',
                    value: this.$store.state.goods.detailGoods.price
                },
                {
                    name: '',
                    value: this.$store.state.goods.detailGoods.number
                },
                {
                    name: '',
                    value: this.$store.state.goods.detailGoods.category
                },
                {
                    name: '',
                    value: this.$store.state.goods.detailGoods.date.getFullYear() + '-' + (this.$store.state.goods.detailGoods.date.getMonth() + 1) + '-' + this.$store.state.goods.detailGoods.date.getDate()
                }
            ],
            chart: null
        }
    },
    mounted () {
        let _this = this;
        this.chart = new Highcharts.Chart(document.getElementById('sales-chart'), {
            chart: {
                type: 'column'
            },
            title: {
                text: ''
            },
            xAxis: {
                categories: [
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                    ''
                ],
                crosshair: true
            },
            yAxis: {
                min: 0,
                title: {
                    text: ' ()'
                }
            },
            tooltip: {
                headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
                pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
                    '<td style="padding:0"><b>{point.y:.1f} </b></td></tr>',
                footerFormat: '</table>',
                shared: true,
                useHTML: true
            },
            plotOptions: {
                column: {
                    pointPadding: 0.2,
                    borderWidth: 0
                }
            },
            series: [
                {
                    name: _this.$store.state.goods.detailGoods.name,
                    data: _this.$store.state.goods.detailGoods.sales
                }
            ]
        });
    },
    methods: {
        changeGoodsDetailFlag () {
            this.setGoodsFlag = true;
        },
        // 
        submitChangeGoods () {
            if (this.$store.state.currUser.coding) {
                if (this.$store.state.currUser.jurisdiction === '') {
                    this.$store.commit('changeGoods', this.setGoods);
                    this.$Message.success('');
                    this.setGoodsFlag = false;
                    let _this = this;
                    let vshopDB = null;
                    IndexedDB.openDB('vshopDB', 1, vshopDB, {
                        name: 'vshop',
                        key: 'name'
                    }, function (db) {
                        let vshopDB = db;
                        IndexedDB.putData(vshopDB, 'vshop', [_this.$store.state.goods]);
                    });
                } else {
                    this.$Message.error('');
                }
            } else {
                this.$Message.error('');
            }
        },
        // 
        deleteGoods () {
            if (this.$store.state.currUser.coding) {
                if (this.$store.state.currUser.jurisdiction === '') {
                    this.$store.commit('deleteGoods');
                    this.$router.push({
                        path: '/goodsDetailList'
                    });
                    let _this = this;
                    let vshopDB = null;
                    IndexedDB.openDB('vshopDB', 1, vshopDB, {
                        name: 'vshop',
                        key: 'name'
                    }, function (db) {
                        let vshopDB = db;
                        IndexedDB.putData(vshopDB, 'vshop', [_this.$store.state.goods]);
                    });
                } else {
                    this.$Message.error('');
                }
            } else {
                this.$Message.error('');
            }
        }
    }
}
</script>

<style>
    h3 {
        padding: 10px;
    } 
    .goods-img div img {
        max-width: 300px;
        max-height: 300px;
    }
</style>



